(function () {
	'use strict';

	angular
		.module('asylumjourneyFrontend')
		.filter('category', category);

	function category() {

		return function categoryFilter(items, category) {
			var filtered = [];

			angular.forEach(items, function(item) {
				angular.forEach(item._embedded.categories, function(itemCategory) {
					if (itemCategory.id === category) {
						filtered.push(item);
					}
				});
			});

			return filtered;
		}
	}
})();

(function() {
	'use strict';

	angular
		.module('asylumjourneyFrontend')
		.run(runBlock);

	/** @ngInject */
	function runBlock(ngDialog, $rootScope) {

		var deregistrationCallback = $rootScope.$on('$routeChangeStart', function(next, current) {
			if (next !== current){
				ngDialog.closeAll();
				$rootScope.dialogOpen = false;
			}
		});


		$rootScope.$on('$destroy', deregistrationCallback)
	}

})();

(function() {
  'use strict';

  angular
    .module('asylumjourneyFrontend')
    .config(routeConfig);

  function routeConfig($routeProvider) {
    $routeProvider
      .when('/', {
        templateUrl: 'app/main/main.html',
        controller: 'MainController',
        controllerAs: 'main'
      })
      .when('/service/:serviceId', {
        templateUrl: 'app/main/main.html',
        controller: 'MainController',
        controllerAs: 'main'
      })
      .otherwise({
        redirectTo: '/'
      });
  }

})();

